
* {
    box-sizing: border-box;
}
body {
    margin:0;
    padding:0;
    height:100;
    background-color: aliceblue;
}
html,
body,
#root,
.container {
    height:100%;
    display: flex;
    justify-content: center;

}
.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;

}
.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding:50px;
    padding-top: 70px;
    width:80%;

}
.movies .movie {
    width: 40%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding:20px;
    color:#adaeb9;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);

   
}
